
@font-face {
  font-family: Roobert;
  src: url("./fonts/RoobertTRIAL-Regular.41f9c4ff.otf") format("opentype")
}
button{
  cursor: pointer;
}

@font-face {
  font-family: Roobert;
  font-weight: 500;
  src: url("./fonts/RoobertTRIAL-Medium.4f71df9a.otf") format("opentype")
}

html {
  font-family: Roobert,system-ui,sans-serif
}

body{
  margin: 0;
}
.flex-center{
  display: flex;
  align-items: end;
  justify-content: center;
}

.app-wrapper {
  padding: 28px 28px;
  color: white;
  border-radius: 31px;
  background: #262839;
  margin: 18px 22px;
  box-sizing: border-box;
}

.secondSectionWrapper {
  border-radius: 31px;
  margin: 20px 22px;
  box-sizing: border-box;
}
.workSection {
  margin: 50px 22px;
  box-sizing: border-box;
  flex-direction: column;
  display: flex;
  gap: 90px;
}

.ccc-cc{
  animation:spin 9s linear infinite;
  position: absolute;
  width: 40vw;
  top: 20%;
  overflow: hidden;
}

@keyframes spin { 
  100% { 
      -webkit-transform: rotate(360deg); 
      transform:rotate(360deg); 
  } 
}

/* below are slider css */
.techHorzintalWrapper {
  width: 100%;
  padding-top: 76px;
  padding-bottom: 20px;
  display: flex;
  overflow: hidden;
}
.techCategiory{
  flex: none;
  animation: loop 90s linear infinite;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 39px;
}
.sliderAlign{
  margin: 0 1.5rem;
}
@keyframes loop {
0% {
  transform: translateX(0%);
}
100% {
  transform: translateX(-100%);
}
}