
.techHorzintalWrapper {
    width: 100%;
    padding-top: 76px;
    padding-bottom: 20px;
    display: flex;
    overflow: hidden;
  }
  .techCategiory{
    flex: none;
    animation: loop 90s linear infinite;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .sliderAlign{
    margin: 0 1.5rem;
  }
  @keyframes loop {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
  }