.workSectionWrapper {
    display: flex;
    justify-content: space-around;
}

.sectionTitle h2 {
    font-size: 52px;
}

.workingCategiory span {
    margin-top: 12px;
    display: block;
    margin-right: 12px;
    padding: 0.8rem 1.2rem;
    box-sizing: border-box;
    box-sizing: border-box;
    width: fit-content;
    background: rgba(0, 0, 0, 5%);
    border-radius: 113px;
    color: rgba(0, 0, 0, .6);
    font-weight: 700;
}

.workingCategiory {
    display: flex;
    flex-wrap: wrap;
}

.sectionTitle {
    display: flex;
    justify-content: space-between;
    padding: 0 4rem;
    align-items: center;
}

.sectionTitle a {
    font-size: 30px;
    color: #bd00ff;
    font-weight: 700;
}
.workSectionDetails h1 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}
.seeAll{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
}
.seeAll a {
    text-decoration: none;
    font-size: larger;
    background: #bf73ff;
    border: none;
    padding: 19px 64px;
    margin: 1rem 0;
    border-radius: 39px;
    color: white;
    text-align: center;
}
.workSectionDetails {
    padding: 0 1.5rem;
}