@media screen and (max-width: 770px) {
    .nav{
        display: none!important;
    }
    .sectionTitle {
        justify-content: space-between;
        padding: 0 0rem!important;
    }
    .serviceTitle {
        padding-left: 0!important;
    }
    article.main-text {
             padding-top: 50px;
    }
    .workSectionDetails {
        padding: 0 0rem!important;
    }

    .workSection {
        gap: 40px!important;
      }
    .workSectionImage {
        place-self: unset;
        width: 100%;
    }
    .workSectionImage img {
        place-self:none ;
        width: 100%;
    }
    .wrapSection{
        margin-top: 40px!important;
        flex-direction: column!important;
        align-items: center!important;
    }
    .sectionServiceCard{
        width: 100%!important;
    }
    .serviceTitle{
        margin-top: 0;
        margin-bottom: 5rem;
    }
    .social-mediaicon {
position: static!important;
margin-top:20px;
    }
    .social-mediaicon ul{
        display: flex;
    }
    .social-mediaicon ul li{
        padding-bottom: 0;
        padding-right: 20px;
    }
    .flex-center {
flex-direction: column-reverse;
align-items: center!important;
    }
    .main-text{
        padding-top: 50px!important;
    }
}

@media screen and (max-width: 470px) {
    .startproject-btn{
        width: 90;
    }
    .ccc-cc{
        width: 60vw!important; 
      }
    .main-text{
text-align: start!important;
    }
    .sectionTitle {
        justify-content: space-between!important;
    }
    .sectionTitle a {
        font-size: 24px!important;
    }
    .sectionTitle h2 {
        font-size: 40px!important;
    }
.app-wrapper{
    margin: 12px 14px;
    height: fit-content;
}
a.startproject-btn{
 font-size: 19px!important;   
 width: 90%!important;
}
.circle-span-project {
    width: 55px!important;
    height: 35px!important;
    margin-left: 12px!important;
}
}
@media screen and (max-width: 978px) {
.workSectionWrapper{
    flex-direction: column;
    gap: 40px;
    justify-content: center!important;
}
.workSectionImage {
    place-self: end;
}
.workSectionTitle {
    width: inherit!important;
    margin-top: 2rem;
}
.seeAll a {
    width: 90%;
}
}


.pol{
    background-image: url("../assests/heroimage/blobshape.svg");
}